// Lib
import React, { Component } from 'react'

// Material UI
import { withStyles, MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles'

    // Main Components
    import TextField from '@material-ui/core/TextField'
    import Select from '@material-ui/core/Select'
    import MenuItem from '@material-ui/core/MenuItem'
    import FormControl from '@material-ui/core/FormControl'
    import InputLabel from '@material-ui/core/InputLabel'
    import Button from '@material-ui/core/Button'
    import CircularProgress from '@material-ui/core/CircularProgress'
    import Icon from '@material-ui/core/Icon'
    import Send from '@material-ui/icons/Send'

    // Colors
    import grey from '@material-ui/core/colors/grey'
    import green from '@material-ui/core/colors/green'

// Config
import { styles, cols } from './styles'
import { up, flc, empty } from '../../../config/style'
import { domainTranslation, locationTranslation, feedbackTranslation } from '../../../config/translations'

// Theme
const theme = createMuiTheme({
    palette: {
        primary: grey,
        secondary: {
            main: grey[200]
        },
    }
 })



const themeStyle = theme => ({
    button: {
        margin: theme.spacing.unit,
        width: 120,
        height: 55,
        left: -85,
        outline: '0px',
    },
    rightIcon: {
        marginLeft: theme.spacing.unit * 1.5,
    },

    buttonProgress: {
        color: green[400],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -20,
        marginLeft: -20,
    },
})








class FormView extends Component {


    state = {
        feedbackText: 'feedback text',

        siteSelect: 'Amager',
        domainSelect: 'sales',


        domains: [ 'sales', 'rental', 'workshop' ],
        sites: [ 'Amager', 'Ballerup', 'BBC', 'Gladsaxe', 'Glostrup', 'Greve', 'KAM', 'Søborg', 'Taastrup' ],


        loading: false,
        clickedPost: false,
    }




    feedbackTextChanged = text => event => {
        // No longer than 200 characters
        if (event.target.value.length < 400) {
            this.setState({ feedbackText: event.target.value })
        }
    }



    siteSelected = event => {
        this.setState({ siteSelect: event.target.value })
    }

    domainSelected = event => {
        this.setState({ domainSelect: event.target.value })
    }




    sendClicked = () => {
        this.setState({ clickedPost: true })
        const { feedbackText, siteSelect, domainSelect } = this.state



        if (!empty(feedbackText) && !empty(siteSelect) && !empty(domainSelect)) {
            this.setState({ loading: true })
            console.log('feedbackText', feedbackText)
            console.log('siteSelect', siteSelect)
            console.log('domainSelect', domainSelect)
        }


    }








    render() {
        const { classes } = this.props
        const { feedbackText, siteSelect, domainSelect, domains, sites, loading, clickedPost } = this.state


        return (
            <div className='container' style={ styles.container }>
                <div className='row'>
                    <h5 className={ cols.formView } style={{ textAlign:'center' }} >{ flc(feedbackTranslation.report) } { flc(feedbackTranslation.feedback) }</h5>

                        <MuiThemeProvider theme={theme}>
                            <div className={ cols.formView }>
                                <TextField
                                    required
                                    fullWidth
                                    multiline
                                    margin="normal"
                                    variant="filled"
                                    variant='outlined'
                                    label={ flc(feedbackTranslation.feedback) }
                                    error={ clickedPost && empty(feedbackText) }

                                    value={ feedbackText }
                                    onChange={this.feedbackTextChanged('name')}
                                />
                            </div>


                            {/* Selectors */}
                            <div className={ cols.formView } style={ styles.inline }>
                                <div className={ cols.siteSelect } style={{ ...styles.siteSelect, ...{ paddingLeft: 0 }}} >
                                    <FormControl style={ styles.formControl }>
                                        <InputLabel error={ clickedPost && empty(siteSelect) }>Location</InputLabel>
                                        <Select
                                            required
                                            fullWidth
                                            value={ siteSelect }
                                            onChange={ this.siteSelected }
                                            error={ clickedPost && empty(siteSelect) }
                                        >
                                            { sites.map( site => (
                                                <MenuItem key={site} value={site}>{ flc(site) }</MenuItem>
                                            )) }
                                        </Select>
                                    </FormControl>
                                </div>



                                <div className={ cols.siteSelect } style={{ ...styles.siteSelect, ...{ paddingRight: 0 }}} >
                                    <FormControl style={ styles.formControl }>
                                        <InputLabel error={ clickedPost && empty(domainSelect) }>{ flc(domainTranslation.domain) }</InputLabel>
                                        <Select
                                            required
                                            fullWidth
                                            value={ domainSelect }
                                            onChange={ this.domainSelected }
                                            error={ clickedPost && empty(domainSelect) }
                                        >
                                            { domains.map( domain => (
                                                <MenuItem key={ domain } value={ domain }>{ flc(domainTranslation[domain]) }</MenuItem>
                                            )) }
                                        </Select>
                                    </FormControl>
                                </div>
                            </div>


                            <div style={ styles.sendButton } className='col-12 offset-6' >
                                <Button
                                    size='large'
                                    variant='raised'
                                    color='secondary'
                                    disabled={ loading }
                                    onClick={ this.sendClicked }
                                    className={classes.button + ' noOutline'}
                                >
                                    Send
                                    <Send className={classes.rightIcon} />
                                    { loading && <CircularProgress size={40} className={ classes.buttonProgress } />}
                                </Button>
                            </div>




                        </MuiThemeProvider>
                </div>


            </div>
        )
    }
}

export default withStyles(themeStyle)(FormView)



